/* Overlay styles */
[data-overlay],
[data-black-overlay] {
  position: relative;
}

[data-overlay] > div,
[data-overlay] > *,
[data-black-overlay] > div,
[data-black-overlay] > * {
  position: relative;
  z-index: 2;
}

[data-overlay]:before,
[data-black-overlay]:before {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
}

[data-overlay]:before {
  background-color: #bd4e45;
}

[data-black-overlay]:before {
  background-color: #000000;
}

[data-overlay="1"]:before,
[data-black-overlay="1"]:before {
  opacity: 0.1;
}

[data-overlay="2"]:before,
[data-black-overlay="2"]:before {
  opacity: 0.2;
}

